.ProfilePicture{
    width: 100%;
    height: 100%;
    object-fit: cover; 
    object-position: center;
}
.ProfilePictureOuter{
    width: 200px;
    height: 200px; 
    overflow: hidden; 
    border-radius: 50%;
}
.center{
    display: flex; 
    align-items: center;
    justify-content: center;
    flex-direction: column;
}